	module.exports = {"pathContext":{"docHtml":"<h1>semver-diff <a href=\"https://travis-ci.org/sindresorhus/semver-diff\"><img src=\"https://travis-ci.org/sindresorhus/semver-diff.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Get the diff type of two <a href=\"https://github.com/isaacs/node-semver\">semver</a> versions: <code>0.0.1 0.0.2</code> → <code>patch</code></p>\n</blockquote>\n<h2>Install</h2>\n<pre><code class=\"language-sh\">$ npm install --save semver-diff\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var semverDiff = require('semver-diff');\n\nsemverDiff('1.1.1', '1.1.2');\n//=> 'patch'\n\nsemverDiff('0.0.1', '1.0.0');\n//=> 'major'\n\nsemverDiff('0.0.1', '0.1.0');\n//=> 'minor'\n\nsemverDiff('0.0.1-foo', '0.0.1-foo.bar');\n//=> 'prerelease'\n\nsemverDiff('0.1.0', '0.1.0+foo');\n//=> 'build'\n\nsemverDiff('0.0.1', '0.0.1');\n//=> null\n\nsemverDiff('0.0.2', '0.0.1');\n//=> null\n</code></pre>\n<h2>API</h2>\n<h3>semverDiff(versionA, versionB)</h3>\n<p>Returns the difference type between two semver versions, or <code>null</code> if they're identical or the second one is lower than the first.</p>\n<p>Possible values: <code>'major'</code>, <code>'minor'</code>, <code>'patch'</code>, <code>'prerelease'</code>, <code>'build'</code>, <code>null</code>.</p>\n<h2>License</h2>\n<p>MIT © <a href=\"http://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/semver-diff/readme","proj":"argo-ci"}}

